.replyBackground{
  overflow-x: hidden;
  width: 100vw;
  margin: 0;
  padding-bottom: 10px;
  background-color: #f7f7f7;
}
.replyWrapper{
  width: 90%;
}

.replyTag{
  padding-top: 20px;
  font-size: 14px;
  font-weight: bold;
  color: rgb(77, 77, 77);
}

.blog-container.blog {
  min-height: calc(100vh - 400px);
}

@media screen and (prefers-color-scheme: dark) { /* dark mode style */
  :root,.loginCard,.blog-container {
    background: var(--darkBackground);
  }

  .replyBackground {
    background-color: var(--darkBackground);
  }

  .form-control {
    background-color: var(--darkContent);
    color: #e4dfd9;
  }

  .header,.divider,.submitButton,.form-group > textarea{
    background: var(--darkContent);
  }

}

@media only screen and (max-width: 1360px) {
  .article-header-stick{
    padding-left: 5vw;
    padding-right: 5vw;
  }

  .title {
    font-size: 30px;
  }

  .blog-body {
    width: 90vw;
    padding: 20px 0;
  }
}

